import React, { Component } from "react";
import Particles from "react-particles-js";

export default class Introduction extends Component {
  render() {
    return (
      <div>
        <section
          id="colorlib-hero"
          className="js-fullheight"
          data-section="home"
        >
          <Particles
            params={{
              particles: {
                number: {
                  value: 80,
                  density: { enable: true, value_area: 800 },
                },
                color: { value: "#ddd" },
                shape: {
                  type: "circle",
                  stroke: { width: 0, color: "#000000" },
                  polygon: { nb_sides: 6 },
                  image: { src: "img/github.svg", width: 100, height: 100 },
                },
                opacity: {
                  value: 0.5,
                  random: false,
                  anim: {
                    enable: true,
                    speed: 1,
                    opacity_min: 0.1,
                    sync: false,
                  },
                },
                size: {
                  value: 3,
                  random: true,
                  anim: {
                    enable: false,
                    speed: 40,
                    size_min: 0.1,
                    sync: false,
                  },
                },
                line_linked: {
                  enable: true,
                  distance: 150,
                  color: "#eeeeee",
                  opacity: 0.2,
                  width: 1,
                },
                move: {
                  enable: true,
                  speed: 2,
                  direction: "none",
                  random: false,
                  straight: false,
                  out_mode: "out",
                  bounce: false,
                  attract: { enable: false, rotateX: 600, rotateY: 1200 },
                },
              },
              interactivity: {
                detect_on: "canvas",
                events: {
                  onhover: { enable: true, mode: "repulse" },
                  onclick: { enable: true, mode: "push" },
                  resize: true,
                },
                modes: {
                  grab: { distance: 400, line_linked: { opacity: 1 } },
                  bubble: {
                    distance: 400,
                    size: 40,
                    duration: 2,
                    opacity: 8,
                    speed: 3,
                  },
                  repulse: { distance: 200, duration: 0.4 },
                  push: { particles_nb: 4 },
                  remove: { particles_nb: 2 },
                },
              },
              retina_detect: true,
            }}
          />
          <div className="desc">
            <h1>
              Hi! <br />
              I'm Tom
            </h1>
            <div className="content__container">
              <ul className="content__container__list">
                <li className="content__container__list__item">
                  Web Developer
                </li>
                <li className="content__container__list__item">
                  Problem solver
                </li>
                <li className="content__container__list__item">
                  World Traveler
                </li>
                <li className="content__container__list__item">
                  Tech enthusiast
                </li>
              </ul>
            </div>
            {/* <p>
              <a
                className="btn btn-primary btn-learn"
                href="#"
                target="_blank"
                rel="noopener noreferrer"
              >
                View CV
                <i className="icon-download4" />
              </a>
            </p> */}
          </div>
        </section>
      </div>
    );
  }
}
